export default {
	props: {
		options: {
			type: Array,
			default: () => []
		},
		active: {
			type: Number,
			default: 1
		}
	},
	data() {
		return {
			selfActive: this.active
		}
	}, 
	methods: {
		selectHandle(e) {
			this.selfActive = e.id
			this.$emit('changeHandle', e)
		}
	}
}
